@import url(https://fonts.googleapis.com/css?family=Roboto:300);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html {
  overflow-x: hidden;
  font-family: Roboto;
}

.App {
  text-align: center;
  background-color: #252525;
  color: #eeeeee;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  /*color: white;*/
}

.App-link {
  /*color: #61dafb;*/
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

nav {
  padding: 1%;
}

link {
  color: #eeeeee;
}


.NameTitle {
  margin-top: 0px;
}

.animate {
  display:inline-block;
  overflow:hidden;
  white-space:nowrap;
}
  
div .wrapper {
  height: 100%;
  font-size: xx-large;
}

.FirstWord {
  -webkit-animation: showup 7s infinite;
          animation: showup 7s infinite;
}

.CatchPhrase {
  width: 0px;
  -webkit-animation: reveal 7s infinite;
          animation: reveal 7s infinite;
}

.CatchPhrase span {
  -webkit-animation: slidein 7s infinite;
          animation: slidein 7s infinite;
}

@-webkit-keyframes showup {
  0% {opacity:0;}
  20% {opacity:1;}
  80% {opacity:1;}
  100% {opacity:0;}
}

@keyframes showup {
  0% {opacity:0;}
  20% {opacity:1;}
  80% {opacity:1;}
  100% {opacity:0;}
}

@-webkit-keyframes slidein {
  0% { margin-left:-800px; }
  20% { margin-left:-800px; }
  35% { margin-left:0px; }
  100% { margin-left:0px; }
}

@keyframes slidein {
  0% { margin-left:-800px; }
  20% { margin-left:-800px; }
  35% { margin-left:0px; }
  100% { margin-left:0px; }
}

@-webkit-keyframes reveal {
  0% {opacity:0;width:0px;}
  20% {opacity:1;width:0px;}
  30% {width:50%;}
  80% {opacity:1;}
  100% {opacity:0;width:102%;}
}

@keyframes reveal {
  0% {opacity:0;width:0px;}
  20% {opacity:1;width:0px;}
  30% {width:50%;}
  80% {opacity:1;}
  100% {opacity:0;width:102%;}
}



.bio {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
}

.text {
    width: 50%;
    text-align: justify; 
}

a {
    color: #00adb5;
    text-decoration: none;
}

.flag {
    width: 32px;
    height: 20px;
}

@media screen and (max-width: 600px) {
    .text {
        width: 80%; 
    }
}

.Profile-picture {
    height: 40vmin;
    pointer-events: none;
    border-radius: 50%;
    margin-top: 20px;
}
footer {
    position: static;
}

.icon {
    max-width:320px;
    max-height:56px;
    margin: 5px;
}

nav a:hover {
    background: #393e46;
  }


@media only screen and (min-width: 200px) and (max-width: 670px) {
    footer {
        position: static;
    }

    .icon {
        max-width:320px;
        max-height:56px;
    }
}


.Tag {
    height: 50%;
    width: 100px;
    border-radius: 5px;
    background-color: #2f3640;
    color: #dcdde1;
    margin: 1%;
    font-size: 70%;
    
}

.Tag:hover {
    background-color: #dcdde1;
    color: #2f3640;
}
.AppScreen {
    -webkit-filter: grayscale(100%);
            filter: grayscale(100%);
}

.Card {
    box-shadow: 0 4px 4px -4px #00adb5;
    -webkit-transition: 0.3s;
    transition: 0.3s;
    width: 30%;
    margin-bottom: 2%;
    position: relative;
}

.Card span {
    visibility: hidden;
    position: absolute;
    top: 50%;
    left: 50%;
    color: #f5f6fa;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    line-height: 100%;
    
}

.Card:hover {
    box-shadow: 0 4px 4px 0 #00adb5;  
}

.Card:hover .AppScreen {
    -webkit-filter: none;
            filter: none;
    -webkit-filter: blur(1px);
            filter: blur(1px);
    opacity: 0.4;
}

.Card:hover span {
    visibility: visible;
    -webkit-transition: all 0.2s linear;
            transition: all 0.2s linear;
}

.Tags {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    align-content: space-between;
    color: #dcdde1;
}

@media screen and (max-width: 600px) {
    .Card {
        width: 100%;
        margin-bottom: 5%; 
    }
}

.Container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
}

@media screen and (max-width: 600px) {
    .Container {
        flex-direction: column;
        justify-content: space-between;
    }
}

